@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}
$heavy-weight: 700;
$horizontal-anim-duration: 90s;
$vertical-anim-duration: 4.5s;

.about {
  position: relative;
  overflow: hidden;

  .background-overlay {
    position: relative;

    .overlay {
      width: 882px;
      height: 433px;
      object-fit: contain;
      mix-blend-mode: multiply;
      will-change: opacity;

      @media screen and (min-width: 1440px) {
        width: 1082px;
        height: 599px;
        margin-top: 0;
      }
    }

    .title-container {
      position: absolute;
      top: 21%;
      left: 8%;

      @media screen and (min-width: 768px) {
        top: 36%;
        left: 14%;
      }
      @media screen and (min-width: 1440px) {
        top: 46%;
        left: 24%;
      }

      .title {
        @extend %standard-font;
        display: inline;
        font-size: 20px;
        font-weight: $heavy-weight;
        color: $color-title !important;

        @media screen and (min-width: 1440px) {
          font-size: 34px;
        }

        .title-sub {
          display: inline;
          font-family: $font-after-night;
          font-weight: $font-weight-normal;
          font-size: 27px;
          color: $color-hover !important;

          @media screen and (min-width: 1440px) {
            font-size: 46px;
          }
        }
      }

      .desc {
        @extend %standard-font;
        font-size: $font-size-small;
        font-weight: $font-weight-normal;
        text-align: left;
        color: $color-title !important;

        @media screen and (min-width: 1440px) {
          font-size: 19.5;
        }
      }

      .play-container {
        display: flex;
        margin-top: 32px;
        justify-content: center;
        position: absolute;

        @media screen and (min-width: 1440px) {
          margin-top: 18px;
        }

        .play-button {
          width: 50px;
          height: 50px;
          opacity: 0.85;
          margin-right: 8px;
        }

        .play-desc {
          margin-top: 6px;

          @extend %standard-font;
          font-weight: $font-weight-light;
          font-size: $font-size-tiny;
          opacity: 0.65;
          text-align: left;
          color: $color-title !important;

          @media screen and (min-width: 1440px) {
            font-size: $font-size-smallest;
          }
        }

        .about-video-container{
          .rodal-mask{
            background-image: linear-gradient(134deg, rgba(0, 215, 175, 0.75),
            rgba(70, 115, 122, 0.75));
            height:102vh;
            width:102vw;
            margin-left:-5px;

            @media screen and (max-width: 820px){
              height:102vh;
              width:102vw;
              top:0vh !important;
              left: 0% !important;
            }
          }

          .rodal-dialog{
            padding: 0 !important;
            border-radius: 20px !important;
            margin-top: 150px;
            width: 640px !important;
            height: 360px !important;

            @media screen and (max-width: 820px){
              width: 426px !important;
              height: 240px !important;
            }
            @media screen and (max-width: 400px){
              width: 320px !important;
              height: 180px !important;
            }

            iframe{
              width:100%;
              height:100%;
              border-radius: 10px !important;
            }
          }
          .hide-close{
            display: none !important;
          }
          .show-close{
            display: block !important;
          }
          .rodal-close{
            display: none;
            height: 50px;
            width: 50px;
            right: -60px;
            top: -55px;
            background-image: url(../../assets/images/common/ExternalCloseIcon.svg);
            background-repeat: no-repeat;
            @media screen and (max-width: 400px){
              display: none !important;
            }
            &:before{
              background:none;
            }
            &:after{
              background:none;
            }
          }
        }
      }
    }
  }

  .pp-logo {
    position: absolute;
    top: 65%;
    right: 27%;
    width: 145px;
    height: 278px;
    object-fit: contain;

    @media screen and (min-width: 375px) {
      top: 65%;
      right: 35%;
      width: 145px;
      height: 278px;
    }
    @media screen and (min-width: 600px) {
      top: 65%;
      right: 40%;
      width: 145px;
      height: 278px;
    }
    @media screen and (min-width: 768px) {
      top: 10%;
      right: 20%;
      width: 252.4px;
      height: 350px;
    }
    @media screen and (min-width: 1440px) {
      top: 15%;
      right: 25%;
      width: 302.4px;
      height: 427px;
    }
  }

  .content {
    .content-section {
      margin-top: 108px;
      margin-bottom: 108px;

      @media screen and (min-width: 1440px) {
        margin-top: 48px;
      }
    }

    .content-row {
      justify-content: center;
      margin-bottom: 24px;
    }

    .title {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;

      @media screen and (min-width: 1440px) {
        font-size: $font-size-extra-big;
      }
    }

    .title-sec {
      @extend %standard-font;
      padding: 10px;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    .title-desc {
      @extend %standard-font;
      font-size: $font-size-smallest;
      color: $color-body;

      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
    }

    .values-container {
      width: 240px;
      height: 400px;
      border-radius: 39px;
      background-image: linear-gradient(to bottom, #00edd8, #00d7af);
      box-shadow: 0 35px 30px 0 rgba(0, 0, 0, 0.05);
      flex-direction: column;
      text-align: center;
      padding-top: 44px;
      margin: 7px 7px;

      .icon {
        width: 60px;
        height: 60px;
      }

      .title {
        @extend %standard-font;
        font-size: $font-size-big;
        font-weight: $heavy-weight;
        padding-top: 46px;
      }

      .desc {
        @extend %standard-font;
        font-size: $font-size-smallest;
        font-weight: $font-weight-normal;
        padding-top: 27px;
      }
    }
    .values-container:nth-child(2) {
      background-image: linear-gradient(to bottom, #ffd138, #ffa41a);
    }
    .values-container:nth-child(3) {
      background-image: linear-gradient(to bottom, #56c1ff, #2b8dff);
    }
    .values-container:nth-child(4) {
      background-image: linear-gradient(to bottom, #ffa0b1, #ff6779);
    }

    .partners-container {
      margin-bottom: 84px;

      .insurer-logo {
        position: relative;
        width: 96px;
        height: 96px;
        border-radius: 50%;
        box-shadow: 0 10px 50px 0 rgba(70, 115, 122, 0.15);
        margin: 16px;

        @media screen and (min-width: 1440px) {
          width: 120px;
          height: 120px;
        }
      }

      /* .insurer-logo-anim {
        animation: wobble $vertical-anim-duration alternate infinite ease-in-out;
      }
      .insurer-logo-anim:nth-child(2n) {
        animation: wobble $vertical-anim-duration alternate infinite ease-in-out;
        animation-delay: 1s;
      }
      .insurer-logo-anim:nth-child(3n) {
        animation: wobble $vertical-anim-duration alternate infinite ease-in-out;
        animation-delay: 2s;
      } */

      .insurers-outer-container {
        position: relative;
        height: 100%;
        overflow: hidden;
        align-content: center;
        padding-bottom: 60px;

        .insurers-container {
          display: grid;
          grid-template-columns: repeat(auto-fit, 110px);
          grid-gap: 2%;
          justify-content: space-evenly;
          position: relative;
          width: 100%;
          animation: scroll-left $horizontal-anim-duration linear infinite;
          padding-bottom: 24px;

          @media screen and (min-width: 1440px) {
            grid-template-columns: repeat(auto-fit, 140px);
          }
        }
        .insurers-container:nth-child(2){
          display: hidden;
          position: absolute;
          left: 100%;
          top: 0;
          animation: scroll-left $horizontal-anim-duration linear infinite;
        }
      }

      .supporter-container {
        display: flex;
        justify-content: center;
      }
    }

    @keyframes scroll-left {
      0%   { transform: translateX(0%); }
      100% { transform: translateX(-100%); }
    }
    @keyframes wobble {
      to { transform: translateY(random(48) - 24 + px); }
      to { transform: translateX(random(16) - 8 + px); }
    }

    .openings-container {
      width: 100%;
      background: $color-form;
      padding: 64px 0px;
      margin: 0px;
      justify-content: center;

      .title-container {
        margin: 5%;
        margin-top: 0;

        @media screen and (min-width: 768px) {
          margin: 0;
          margin-right: 2.5%;
        }
        @media screen and (min-width: 1440px) {
          margin: 0;
          margin-right: 5%;
        }

        .title {
          display: inline;
          @extend %standard-font;
          font-weight: $font-weight-semibold;
          font-size: 26px;
          color: $color-title;

          .desc {
            display: inline;
            font-weight: $font-weight-normal;
          }
        }
      }

      .button {
        margin-left: 10%;
      }
    }


  }

  .carousel-indicators {
    margin: 0;

    li {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: $color-line;
      margin: 0px 4px;
    }
    .active {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: $color-hover;
      margin: 0px 4px;
    }
  }

  .inverted-comma-bottom {
    position: absolute;
    bottom: -2%;
    width: 124px;
    height: 111px;
    right: 0;
    opacity: 0.5;
    transform: rotate(180deg) scale(0.3);
    z-index: 99;

    @media screen and (min-width: 768px) {
      top: 80%;
      right: 15%;
      transform: rotate(180deg) scale(0.5);
    }
    @media screen and (min-width: 1440px) {
      width: 154px;
      height: 141px;
      top: 80%;
      right: 15%;
      transform: rotate(180deg) scale(0.7);
    }
  }
  .subscribe-section {
    margin-bottom:  0%;
  }
}

.testimonials {
  justify-content: center;
  padding-bottom: 32px;

  &.blue {
    padding-bottom: 0px;
  }

  .testimonial-container {

    @media screen and(min-width: 820px){
    }

    .inverted-comma-bottom {
      width: 124px;
      height: 111px;
      transform: rotate(180deg) scale(0.5);

      @media screen and (min-width: 768px) {
        margin-left: 50%;
      }
      @media screen and (min-width: 1440px) {
        width: 154px;
        height: 141px;
        transform: rotate(180deg) scale(0.7);
      }
    }
  }

  .title-container {
    position: relative;
    padding: 15% 0 0 0;
    text-align: center;
    align-items: flex-end;
    justify-content: center;

    @media screen and (min-width: 768px) {
      padding: 10% 0 0 0;
    }
    @media screen and (min-width: 1440px) {
      padding: 5% 0 0 0;
    }

    &.blue {
      padding: 10% 0 0 0;

      @media screen and (min-width: 768px) {
        padding: 5% 0 0 0;
      }
    }

    .inverted-comma-top {
      position: absolute;
      opacity: 0.8;
      color: $color-hover;
      width: 74px;
      height: 71px;
      left: 10%;
      top: -8%;

      @media screen and (min-width: 768px) {
        margin-right: 5%;
        width: 124px;
        height: 111px;
      }
      @media screen and (min-width: 1440px) {
        left: 15%;
        top: -10%;
        margin-right: 5%;
        width: 174px;
        height: 161px;
      }

      &.blue {
        position: relative;
        left: 0px;
        transform: scale(0.8);

        @media screen and (min-width: 768px) {
          margin-right: 3%;
          left: 4%;
          bottom: -8px;
        }
        @media screen and (min-width: 1440px) {
          bottom: -20px;
        }
      }
    }

    .desc {
      display: inline;
      @extend %standard-font;
      padding: 0 30px;
      font-size: $font-size-regular;
      font-weight: $font-weight-semibold;
      color: $color-body;

      &.blue {
        font-weight: $font-weight-bold;
        color: $color-input-form-landing-desc;
      }
    }

    .desc-bold {
      display: inline;
      @extend %standard-font;
      font-size: 30px;
      font-weight: $heavy-weight;
      color: $color-body;

      &.blue {
        font-weight: $font-weight-bold;
        font-size: $font-size-regular;
        color: $color-input-form-landing-desc;
      }
    }

    .desc-cursive {
      @extend %standard-font;
      font-family: $font-after-night;
      font-size: 48px;
      color: $color-hover;
      margin-top: 8px;

      &.blue {
        color: $color-input-text;
      }
    }
  }

  .testimonial-group {
    justify-content: center;
    margin-top: 48px;
    margin-bottom: 84px;

    &.blue {
      margin-bottom: 0px;
    }

    .testimonial {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;

      .contents {
        position: relative;
        top: -48px;
        margin: 0px 16px;
        padding-top: 74px;
        width: 225px;
        height: 290px;
        border-radius: 30px;
        box-shadow: -4px 74px 45px 0 rgba(70, 115, 122, 0.15);
        background-color: rgba(255, 255, 255, 0.9);
        border: solid 1px $color-form;

        @media screen and (min-width: 1440px) {
          top: -64px;
        }

        .name {
          @extend %standard-font;
          font-weight: $font-weight-semibold;
          color: $color-hover;
          font-size: $font-size-regular;

          &.blue {
            color: $color-input-text;
          }
        }

        .jobtitle {
          @extend %standard-font;
          margin-top: 4px;
          font-weight: $font-weight-semibold;
          color: $color-title;
          font-size: $font-size-smallest;

          &.blue {
            color: $color-input-form-landing-desc;
          }
        }

        .desc {
          @extend %standard-font;
          padding: 18px 40px 40px;
          color: $color-body;
          font-size: $font-size-tiny;

          &.blue {
            color: $color-input-form-landing-desc2;
          }
        }
      }

      .profile-photo {
        width: 96px;
        height: 96px;
        border-radius: 50%;
        box-shadow: 0 2px 4px 0 rgba(70, 115, 122, 0.1);
        z-index: 1;

        @media screen and (min-width: 1440px) {
          width: 120px;
          height: 120px;
        }
      }
    }
  }
}
